.advantages__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
}
.advantages__item {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
  background-color: #eeeeee;
}
.advantages__item:first-child {
  position: relative;
  overflow: hidden;
}
.advantages__item:first-child::before {
  content: "";
  position: absolute;
  display: block;
  margin: 0 auto;
  z-index: 1;
  background-color: transparent;
  background-image: url("../img/divider-bottom.svg");
