  color: #ffffff;
}
.form-label {
  display: block;
  margin-left: 18px;
}
@media (max-width: 767px) {
  .form__title {
    display: block;
    padding-top: 24px;
    padding-bottom: 20px;
  }
  .form-wrapper_area {
    border-bottom: 1px solid #e5e5e5;
  }
  .form__group {
    border-bottom: 1px solid #e5e5e5;
    padding-bottom: 34px;
  }
  .form-name {
    padding-bottom: 16px;
  }
  .form-surname {
    padding-bottom: 16px;
  }
}
@media (min-width: 768px) {
  .form-area_contact {
    justify-content: start;
  }
}
.form-name {
  display: flex;
  flex-wrap: wrap;
  align-items: start;
  flex-direction: column;
  justify-content: space-between;
}
.form-surname {
  display: flex;
  flex-wrap: wrap;
  align-items: start;
  flex-direction: column;
  justify-content: space-between;
}
.form-patronymic {
  display: flex;
  flex-wrap: wrap;
  align-items: start;
  flex-direction: column;
  justify-content: space-between;
  padding-bottom: 39px;
}
.form-phone {
  display: flex;
  flex-wrap: wrap;
  align-items: start;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  padding-bottom: 16px;
}
.form-phone .form-icon__wrapper {
  display: block;
  position: absolute;
  width: 58px;
  height: 58px;
  background-color: #e6e6e6;
  right: 0;
  top: 37px;
}
@media (max-width: 767px) {
  .form-phone .form-icon__wrapper {
    top: 21px;
  }
}
.form-phone .form-icon__wrapper .form-icon__phone {
  margin-top: 20px;
}
.form-email {
  display: flex;
  flex-wrap: wrap;
  align-items: start;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  padding-bottom: 34px;
}
.form-email .form-icon__wrapper {
  display: block;
  position: absolute;
  width: 58px;
  height: 58px;
  background-color: #e6e6e6;
  right: 0;
  top: 37px;
}
@media (max-width: 767px) {
  .form-email .form-icon__wrapper {
    top: 21px;
  }
}
.form-email .form-icon__wrapper .form-icon__mail {
  margin-top: 23px;
}
.form__item {
  display: flex;
  padding-top: 32px;
}
.form__label {
  align-items: center;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  padding-left: 62px;
  padding-bottom: 18px;
  padding-top: 18px;
}
.form__control {
  position: relative;
  display: block;
  padding-left: 20px;
}
.form__control:hover .form__mark {
  background-color: #e5e5e5;
}
.form__control:focus .form__mark {
  background-color: #f2f2f2;
  outline: 2px solid #e5e5e5;
}
.form__control:active .form__mark {
  background-color: #d5d5d5;
}
.form__control:active .form__mark::before {
  opacity: 0.3;
}
.form__control:active .form__mark::after {
  opacity: 0.3;
}
.form__mark {
  position: absolute;
  width: 46px;
  height: 46px;
  background-color: #f2f2f2;
  border-radius: 4px;
  top: -8px;
  left: 0;
}
.form__input[type="checkbox"]:checked + .form__mark::before,
.form__input[type="checkbox"]:checked + .form__mark::after {
  content: "";
  position: absolute;
  background-color: #333333;
}
.form__input[type="checkbox"]:checked + .form__mark::before {
  transform: rotate(45deg);
  top: 26px;
  left: 12px;
  width: 11px;
  height: 4px;
}
.form__input[type="checkbox"]:checked + .form__mark::after {
  transform: rotate(-45deg);
  top: 22px;
  left: 16px;
  width: 22px;
  height: 4px;
}
.form__input[type="radio"] + .form__mark {
  border-radius: 50%;
}
.form__input[type="radio"]:checked + .form__mark::before {
  content: "";
  position: absolute;
  top: 16px;
  left: 16px;
  width: 16px;
  height: 16px;
  background-color: #333333;
  border-radius: 50%;
}
.modal-container {
  position: fixed;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  flex-direction: column;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 2;
  display: none;
}
.modal-error {
  position: relative;
  margin: auto;
  background-color: #ffffff;
  width: 320px;
  padding-bottom: 35px;
}
.modal-error .modal-slogan {
  width: 237px;
  padding: 18px 42px 28px 42px;
  margin: 0 auto;
}
.modal-error .modal-button {
  padding: 18px 64px 14px;
}
.modal-error .modal-title {
  margin: 0 auto;
  padding-top: 45px;
}
.modal-success {
  position: relative;
  margin: auto;
  background-color: #ffffff;
  width: 320px;
  padding-top: 44px;
}
.modal-success .modal-slogan {
  width: 242px;
  padding: 18px 39px 34px 39px;
}
.modal-success .modal-button {
  width: 100%;
  padding: 18px 64px 14px;
  border: 20px solid #f2f2f2;
}
.modal-title {
  margin: 0 auto;
  text-transform: uppercase;
}
.modal-slogan {
  margin: 0 auto;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .modal-error {
    width: 400px;
    padding-bottom: 49px;
  }
  .modal-error .modal-slogan {
    display: block;
    width: 307px;
    padding: 18px 47px 28px 47px;
  }
  .modal-error .modal-button {
    padding: 18px 122px 14px;
  }
  .modal-error .modal-title {
    display: block;
    width: 250px;
    padding-top: 45px;
    font-size: 30px;
    line-height: 40px;
  }
  .modal-success {
    width: 640px;
    padding-top: 44px;
  }
  .modal-success .modal-slogan {
    display: block;
    width: 444px;
    padding: 18px 39px 34px 39px;
  }
  .modal-success .modal-title {
    font-size: 30px;
    line-height: 40px;
  }
  .modal-success .modal-button {
    width: 100%;
    padding: 18px 64px 14px;
    border-top: 44px solid #f2f2f2;
    border-bottom: 44px solid #f2f2f2;
    border-left: 91px solid #f2f2f2;
    border-right: 91px solid #f2f2f2;
  }
}
.map {
  background-color: #615048;
  width: 320px;
}
.map iframe {
  display: block;
  border: none;
  margin: 0 auto;
  width: 100%;
}
@media (min-width: 768px) {
  .map {
    width: 768px;
  }
}
@media (min-width: 1200px) {
  .map {
    width: 1200px;
  }
}
.map-image {
  display: block;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .map img {
    width: 768px;
    height: 350px;
  }
}
@media (min-width: 1200px) {
  .map img {
    width: 1200px;
    height: 350px;
  }
}
